import Vue from 'vue'
import VueRouter from 'vue-router'
import {
  appRoutes,
  serviciosRoutes,
  blogsRoutes,
  eventosRoutes,
  errorsRoutes
} from './routes'


Vue.use(VueRouter)

const routes = [
  ...appRoutes,
  ...serviciosRoutes,
  ...blogsRoutes,
  ...eventosRoutes,
  ...errorsRoutes
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

export default router
